.section {
    display: flex;
    flex-direction: column;
    width: 500px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 20px;
    background-color: rgb(207, 207, 220);
    border: 2px solid gray;
    border-radius: 8px;
    box-shadow: 0px 3px 5px 0px #000000;
}

.title {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 20px;
}

.list {
    margin: 0;
    padding: 0;
}

.text {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-weight: bold;
    text-align: start;
}